<template>
  <div>
    <content-title
      title="已处理邮件(邮件过滤)"
      tips="此处显示已过滤邮件列表。">
    </content-title>
    <nav-bar :nav="nav" @navBack="navBack">
      <el-input
        class="com-search"
        placeholder="用户名/ID"
        v-model="searchVal"
        @change.native="searchHandle">
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>
    </nav-bar>
    <content-c>
      <el-table
        :data="resumeList"
        style="width: 100%">
        <el-table-column
          prop="status.value"
          label="处理状态">
          <template slot-scope="scope">
            <span v-if="currentNav.statusId === 4">已通过</span>
            <span v-if="currentNav.statusId === 3">淘汰/屏蔽</span>
            <span v-if="currentNav.statusId === 1">处理失败</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="username"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="job.name"
          label="岗位">
        </el-table-column>
        <el-table-column
          prop="form.value"
          label="来源">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="mailSize"
          label="大小(kb)">
        </el-table-column>
        <el-table-column
          prop="integral"
          label="积分">
        </el-table-column>

        <el-table-column
          prop="currentStatus.value"
          v-if="currentNav.statusId === 4"
          label="当前状态">
        </el-table-column>
        <el-table-column
          prop="remarks"
          v-if="currentNav.statusId === 3"
          label="淘汰/屏蔽原因">
        </el-table-column>

        <el-table-column
          prop="processTime"
          label="处理时间">
          <template slot-scope="scope">
            {{getTimeFormat(scope.row.operationTime)}}
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="360">
          <template slot-scope="scope">
            <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
            <button class="com-btn com-edit" @click="handleEmailDetail(scope.row)">邮件正文</button>
            <button class="com-btn com-history" @click="handleHistory(scope.row)">历史状态</button>
          </template>
        </el-table-column>
      </el-table>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="resumeListTotal">
          </el-pagination>
        </div>
      </div>
    </content-c>

    <!--查看-->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">处理状态:</span>{{resumeData.processedStatus}}</p>
        <p><span class="label">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:</span>{{resumeData.name}}</p>
        <p><span class="label">岗&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位:</span>{{resumeData.pos}}</p>
        <p><span class="label">来&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;源:</span>{{resumeData.from}}</p>
        <p><span
          class="label">I&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;D:</span>{{resumeData.id}}
        </p>
        <p><span class="label">大&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;小:</span>{{resumeData.size}}kb</p>
        <p><span class="label">积&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;分:</span>{{resumeData.integral}}分</p>
        <p><span class="label">处理时间:</span>{{resumeData.processedTime}}</p>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        查看信息
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--邮件正文-->
    <com-dialog
      :show="emailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;题:</span>{{resumeData.mailTitle}}</p>
        <p><span
          class="label">时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;间:</span>{{getTimeFormat(resumeData.mailSendTime)}}
        </p>
        <p><span class="label">收&nbsp;&nbsp;件&nbsp;&nbsp;人:</span>{{resumeData.mailId}}</p>

        <p class="com-dashed-line"></p>

        <p><span class="label label-block">
          正&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;文:</span>
          <a target="_blank" :href="serverBaseUrlCpt + resumeData.mailSavePath">点击查看</a>
        </p>

        <p class="com-dashed-line"></p>

        <p>
          <span class="label label-block">附&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;件:</span>
          <span class="label-block" v-for="f in resumeData.enclosures">{{f.name}}&nbsp;&nbsp;<a target="_blank" :href="serverBaseUrlCpt + f.path">下载</a></span>
        </p>
      </div>

      <div slot="title" class="com-dialog-title t-2">
        邮件正文
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--邮件历史状态-->
    <com-dialog
      :show="historyStatusDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <el-table
        :data="resumeHistoryList"
        style="width: 100%">
        <el-table-column
          prop="processTime"
          label="处理时间">
          <template slot-scope="scope">
            {{getTimeFormat(scope.row.operationTime)}}
          </template>
        </el-table-column>

        <el-table-column
          prop="statusSmall.value"
          label="状态">
        </el-table-column>

        <el-table-column
          prop="id"
          label="ID">
        </el-table-column>

        <el-table-column
          prop="operationUser.name"
          label="操作人员">
        </el-table-column>

      </el-table>

      <!--<div class="com-page-box-dialog">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="resumeListTotal">
          </el-pagination>
        </div>
      </div>-->

      <div slot="title" class="com-dialog-title t-3">
        历史状态
        <!--<div class="close x-color-3" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #5AD1D1; border-color: #5AD1D1;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import navBar from '../../componentsBody/navBar.vue'
  import { mapState, mapActions, mapMutations } from 'vuex'
  import { formatDateTime } from '../../../utils/utils'
  import { serverBaseUrl } from '../../../utils/baseConfig'
  import comDialog from '../../../components/toast/dialog.vue'

  export default {
    name: '',
    props: {},
    data () {
      return {
        nav: [
          {name: '已通过', statusId: 4},
          {name: '已淘汰', statusId: 3},
          {name: '处理失败', statusId: 1},
        ],
        currentNav: {},
        searchVal: '',
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        resumeData: {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedTime: ''
        },
        detailDialogVisible: false,
        emailDialogVisible: false,
        historyStatusDialogVisible: false,
      }
    },
    computed: {
      ...mapState('resume', [
        'resumeList',
        'resumeListTotal',
        'resumeHistoryList',
      ]),
      serverBaseUrlCpt () {
        return serverBaseUrl
      }
    },
    methods: {
      ...mapActions('resume', [
        'ac_resumeList',
        'ac_resumeHistoryList',
      ]),
      ...mapMutations('resume', [
        'mut_resumeList',
      ]),
      navBack (obj) {
        this.currentNav = obj.nav
        this.ac_resumeList({
          entity: {statusBig: {id: this.currentNav.statusId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_resumeList({
          entity: {statusBig: {id: this.currentNav.statusId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_resumeList({
          entity: {statusBig: {id: this.currentNav.statusId}},
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      handleDetail (row) {
        this.detailDialogVisible = true
        let statusVal = ''
        if (this.currentNav.statusId === 4) {
          statusVal = '已通过'
        } else if (this.currentNav.statusId === 3) {
          statusVal = '淘汰/屏蔽'
        } else if (this.currentNav.statusId === 1) {
          statusVal = '处理失败'
        }
        this.resumeData = {
          processedStatus: statusVal,
          name: row.username,
          pos: row.job ? row.job.name : '',
          from: row.form ? row.form.value : '',
          id: row.id,
          size: row.mailSize,
          integral: row.integral,
          processedTime: this.getTimeFormat(row.operationTime)
        }
      },
      handleEmailDetail (row) {
        this.emailDialogVisible = true
        this.resumeData = row
      },
      handleHistory (row) {
        this.historyStatusDialogVisible = true
        this.ac_resumeHistoryList({
          resume: {id: row.id}
        })
      },
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      closeDialog () {
        this.detailDialogVisible = false
        this.emailDialogVisible = false
        this.historyStatusDialogVisible = false
        this.resumeData = {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedTime: ''
        }
      },
      searchHandle () {
        if (this.searchVal) {
          this.ac_resumeList({
            entity: {statusBig: {id: this.currentNav.statusId}, key: this.searchVal},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        } else {
          this.ac_resumeList({
            entity: {statusBig: {id: this.currentNav.statusId}},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        }
      }
    },
    components: {
      contentTitle,
      contentC,
      navBar,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.currentNav = this.nav[0]
      this.ac_resumeList({
        entity: {statusBig: {id: this.nav[0].statusId}},
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {
      this.mut_resumeList([])
    },
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #FDC61E;
  }

  .x-color-2 {
    color: #FDC61E;
  }

  .t-3 {
    background-color: #5AD1D1;
  }

  .x-color-3 {
    color: #5AD1D1;
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
  }
</style>



